<template>
    <div :class="['vue-heart-rating', {'vue-heart-rating-rtl':rtl}, {'vue-heart-rating-inline': inline}]">
      <div class="sr-only">
        <slot
          name="screen-reader"
          :rating="selectedRating"
          :hearts="maxRating"
        >
          <span>Rated {{ selectedRating }} hearts out of {{ maxRating }}</span>
        </slot>
      </div>
  
      <div
        class="vue-heart-rating"
        @mouseleave="resetRating"
      >
        <span
          v-for="n in maxRating"
          :key="n"
          :class="[{'vue-heart-rating-pointer': !readOnly }, 'vue-heart-rating-heart']"
          :style="{'margin-right': margin + 'px'}"
        >
          <heart
            :fill="fillLevel[n-1]"
            :size="heartSize"
            :heart-id="n"
            :step="step"
            :active-color="currentActiveColor"
            :inactive-color="inactiveColor"
            :border-color="borderColor"
            :active-border-color="currentActiveBorderColor"
            :border-width="borderWidth"
            :rounded-corners="roundedCorners"
            :rtl="rtl"
            :glow="glow"
            :glow-color="glowColor"
            :animate="animate"
            @heart-selected="setRating($event, true)"
            @heart-mouse-move="setRating"
          />
        </span>
        <span
          v-if="showRating"
          :class="['vue-heart-rating-rating-text', textClass]"
        > {{ formattedRating }}</span>
      </div>
    </div>
  </template>
  
  <script type="text/javascript">
  /* eslint-disable vue/custom-event-name-casing */
  import Heart from './heart.vue'
  
  export default {
    name: 'VueHeartRating',
    components: {
      Heart
    },
    props: {
      increment: {
        type: Number,
        default: 1
      },
      rating: {
        type: Number,
        default: 0
      },
      roundStartRating: {
        type: Boolean,
        default: true
      },
      activeColor: {
        type: [String, Array],
        default: '#ffd055'
      },
      inactiveColor: {
        type: String,
        default: '#d8d8d8'
      },
      maxRating: {
        type: Number,
        default: 5
      },
      heartSize: {
        type: Number,
        default: 50
      },
      showRating: {
        type: Boolean,
        default: true
      },
      readOnly: {
        type: Boolean,
        default: false
      },
      textClass: {
        type: String,
        default: ''
      },
      inline: {
        type: Boolean,
        default: false
      },
      borderColor: {
        type: String,
        default: '#999'
      },
      activeBorderColor: {
        type: [String, Array],
        default: null
      },
      borderWidth: {
        type: Number,
        default: 0
      },
      roundedCorners: {
        type: Boolean,
        default: false
      },
      padding: {
        type: Number,
        default: 0
      },
      rtl: {
        type: Boolean,
        default: false
      },
      fixedPoints: {
        type: Number,
        default: null
      },
      glow: {
        type: Number,
        default: 0
      },
      glowColor: {
        type: String,
        default: '#fff'
      },
      clearable: {
        type: Boolean,
        default: false
      },
      activeOnClick: {
        type: Boolean,
        default: false
      },
      animate: {
        type: Boolean,
        default: false
      }
    },
    emits: ['update:rating', 'hover:rating'],
  
    data() {
      return {
        step: 0,
        fillLevel: [],
        currentRating: 0,
        selectedRating: 0,
        ratingSelected: false
      }
    },
    computed: {
      formattedRating() {
        return (this.fixedPoints === null) ? this.currentRating : this.currentRating.toFixed(this.fixedPoints)
      },
      shouldRound() {
        return this.ratingSelected || this.roundStartRating
      },
      margin() {
        return this.padding + this.borderWidth
      },
      activeColors() {
        if (Array.isArray(this.activeColor)) {
          return this.padColors(this.activeColor, this.maxRating, this.activeColor.slice(-1)[0])
        }
  
        return new Array(this.maxRating).fill(this.activeColor)
      },
      currentActiveColor() {
        if (!this.activeOnClick) {
          return (this.currentRating > 0) ? this.activeColors[Math.ceil(this.currentRating) - 1] : this.inactiveColor
  
        }
        return (this.selectedRating > 0) ? this.activeColors[Math.ceil(this.selectedRating) - 1] : this.inactiveColor
      },
      activeBorderColors() {
        if (Array.isArray(this.activeBorderColor)) {
          return this.padColors(this.activeBorderColor, this.maxRating, this.activeBorderColor.slice(-1)[0])
        }
        let borderColor = (this.activeBorderColor) ? this.activeBorderColor : this.borderColor
        return new Array(this.maxRating).fill(borderColor)
      },
      currentActiveBorderColor() {
        if (!this.activeOnClick) {
          return (this.currentRating > 0) ? this.activeBorderColors[Math.ceil(this.currentRating) - 1] : this.borderColor
  
        }
        return (this.selectedRating > 0) ? this.activeBorderColors[Math.ceil(this.selectedRating) - 1] : this.borderColor
      },
      roundedRating() {
        let inv = 1.0 / this.increment
        return Math.min(this.maxRating, Math.ceil(this.currentRating * inv) / inv)
      }
    },
    watch: {
      rating(val) {
        this.currentRating = val
        this.selectedRating = val
        this.createHearts(this.shouldRound)
      }
    },
    created() {
      this.step = this.increment * 100
      this.currentRating = this.rating
      this.selectedRating = this.currentRating
      this.createHearts(this.roundStartRating)
    },
    methods: {
      setRating($event, persist) {
        if (!this.readOnly) {
          const position = (this.rtl) ? (100 - $event.position) / 100 : $event.position / 100
          this.currentRating = (($event.id + position) - 1).toFixed(2)
          this.currentRating = (this.currentRating > this.maxRating) ? this.maxRating : this.currentRating
          if (persist) {
            this.createHearts(true, true)
            this.selectedRating = (this.clearable && this.currentRating === this.selectedRating) ? 0 : this.currentRating
            this.$emit('update:rating', this.selectedRating)
            this.ratingSelected = true
  
          } else {
            this.createHearts(true, !this.activeOnClick)
            this.$emit('hover:rating', this.currentRating)
          }
        }
      },
      resetRating() {
        if (!this.readOnly) {
          this.currentRating = this.selectedRating
          this.createHearts(this.shouldRound)
        }
      },
      createHearts(round = true, applyFill = true) {
        this.currentRating = (round) ? this.roundedRating : this.currentRating
        for (let i = 0; i < this.maxRating; i++) {
          let level = 0
          if (i < this.currentRating) {
            level = (this.currentRating - i > 1) ? 100 : (this.currentRating - i) * 100
          }
          if (applyFill) {
            this.fillLevel[i] = Math.round(level)
          }
        }
      },
      padColors(array, minLength, fillValue) {
        return Object.assign(new Array(minLength).fill(fillValue), array)
      }
    }
  }
  </script>
  
  <style scoped>
  .vue-heart-rating-heart {
    display: inline-block;
    -webkit-tap-highlight-color: transparent;
  }
  
  .vue-heart-rating-pointer {
    cursor: pointer;
  }
  
  .vue-heart-rating {
    display: flex;
    align-items: center;
  }
  
  .vue-heart-rating-inline {
    display: inline-flex;
  }
  
  .vue-heart-rating-rating-text {
    margin-left: 7px;
  }
  
  .vue-heart-rating-rtl {
    direction: rtl;
  }
  
  .vue-heart-rating-rtl .vue-heart-rating-rating-text {
    margin-right: 10px;
    direction: rtl;
  }
  
  .sr-only {
    position: absolute;
    left: -10000px;
    top: auto;
    width: 1px;
    height: 1px;
    overflow: hidden;
  }
  </style>
  